body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.header-top {
  color: #fff;
  background-color: #333;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.header-logo {
  width: 50px;
  height: auto;
}

.searchbox {
  background-color: #fff;
  padding: 10px;
}

.container {
  flex-direction: row;
  height: calc(100vh - 100px);
  display: flex;
}

.sidebar {
  box-sizing: border-box;
  background-color: #f4f4f4;
  flex-direction: column;
  gap: 20px;
  width: 20%;
  padding: 20px;
  display: flex;
}

.main-content {
  box-sizing: border-box;
  background-color: #fff;
  flex: 1;
  padding: 20px;
}

#hits {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  display: grid;
}

.hit {
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 10px;
}

#pagination {
  margin-top: 20px;
}
